<template>
  <div class="m-header">
    <span class="icon"></span>
    <h1 class="title">{{ title }}</h1>
  </div>
</template>

<script>
export default {
  name: 'MHeader',
  data() {
    return {
      title: 'F-Music'
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable';
@import '~common/scss/mixin';
.m-header {
  height: 44px;
  line-height: 44px;
  text-align: center;
  font-size: 0;
  .icon {
    display: inline-block;
    margin-right: 9px;
    vertical-align: middle;
    width: 30px;
    height: 32px;
    background: center/100% no-repeat;
    @include bg-image('logo');
  }
  .title {
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    font-size: $font-size-large;
    color: $color-theme;
  }
}
</style>



