<template>
  <div class="scroll-wrapper" ref="scroll">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from '@better-scroll/core'
export default {
  name: 'scroll',
  data() {
    return {
      bs: null
    }
  },
  props: {
    dataList: {
      type: Array,
      default: null
    }
  },
  mounted() {
    setTimeout(() => {
      this.bs = new BScroll(this.$refs.scroll, {
        scrollX: false,
        scrollY: true,
        probeType: 3,
        useTransition: false // 滑动闪烁问题
      })
      this.bs.on('scroll', (position) => {
        this.$emit('scroll', position.y)
      })
    }, 20)
  },
  watch: {
    dataList() {
      if (this.bs) {
        setTimeout(() => {
          this.bs.refresh()
        }, 20)
      }
    }
  },
  components: {},
  methods: {
    scrollTo(y) {
      this.bs.scrollTo(0, y, 400)
    },
    scrollToElement(ele) {
      this.bs.scrollToElement(ele)
    },
    refresh() {
      this.bs && this.bs.refresh()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-wrapper {
  height: 100%;
}
</style>



