<template>
  <div class="singer-detail">
    <div @click="goSinger" class="icon-container">
      <i class="icon-back"></i>
    </div>
    <h2 class="title">{{ singer.name }}</h2>
    <div class="singer-poster" :style="bgStyle" ref="singerPoster">
      <div class="mask" ref="mask"></div>
      <div class="filter" ref="filter"></div>
    </div>
    <div class="song-list-wrap" ref="songListWrapper">
      <scroll @scroll="onScroll" :dataList="songList">
        <music-list :songList="songList"></music-list>
      </scroll>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { getSingerDetail } from '../../api/singer'
import { NO_ERROR } from '../../api/config'
import { createSong } from '../../common/js/model/song'
import { prefixStyle } from '../../common/js/dom'
import Scroll from '../../base/scroll/scroll.vue'
import MusicList from '../music-list/music-list.vue'

const TOP_HEIGHT = 40
const transform = prefixStyle('transform')
const backdropFilter = prefixStyle('backdropFilter')
export default {
  name: 'SingerDetail',
  data() {
    return {
      songList: [],
      scrollY: 0
    }
  },
  created() {
    this._initSinger()
  },
  mounted() {
    this.singerPosterHeight = this.$refs.singerPoster.clientHeight
    this.$refs.songListWrapper.style.top = this.singerPosterHeight + 'px'
    this.minTransalteY = -this.singerPosterHeight + TOP_HEIGHT
  },
  components: { Scroll, MusicList },
  methods: {
    onScroll(positionY) {
      this.scrollY = positionY
    },
    goSinger() {
      this.$router.push('/singers')
    },
    _initSinger() {
      if (!this.singer || !this.singer.id) {
        this.goSinger()
      }
      this.bgStyle = {
        backgroundImage: `url(${this.singer.avatar})`
      }
      this._getSingerDetail(this.singer.id)
    },
    _getSingerDetail(id) {
      getSingerDetail(id).then((data) => {
        if (data.code === NO_ERROR) {
          this.songList = data.data.list.map((item) =>
            createSong(item.musicData)
          )
        }
      })
    }
  },
  watch: {
    scrollY(newVal) {
      let zIndex = 0
      let singerPosterHeight = 0
      let padding = '70%'
      let maskHeight = 0
      let blur = 0
      const precent = Math.abs(newVal / this.singerPosterHeight)
      if (newVal <= 0) {
        blur = Math.min(20, precent * 20)
        maskHeight = Math.min(
          Math.abs(newVal),
          this.singerPosterHeight - TOP_HEIGHT
        )
        this.$refs.singerPoster.style[transform] = 'unset'
        if (
          this.singerPosterHeight - TOP_HEIGHT <=
          Math.min(Math.abs(newVal))
        ) {
          zIndex = 10
          padding = 0
          singerPosterHeight = TOP_HEIGHT
          maskHeight = 0
        }
      } else {
        this.$refs.singerPoster.style[transform] = `scale3d(${1 + precent},${
          1 + precent
        },1)`
        // this.$refs.singerPoster.style[transform] = `scale(${percent})`
      }
      this.$refs.mask.style.height = maskHeight + 'px'
      this.$refs.singerPoster.style.zIndex = zIndex
      this.$refs.singerPoster.style.height = singerPosterHeight + 'px'
      this.$refs.singerPoster.style.paddingTop = padding
      // eslint-disable-next-line standard/computed-property-even-spacing
      this.$refs.filter.style[backdropFilter] = `blur(${blur}px)`
      // this.$refs.filter.style['backgroundColor'] = 'red'
    }
  },
  computed: {
    ...mapGetters(['singer'])
  }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable.scss';
.singer-detail {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  color: $color-theme;
  background: $color-background;
  .icon-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    z-index: $z-index-top;
  }
  .title {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 40px;
    line-height: 40px;
    z-index: $z-index-top;
  }

  .singer-poster {
    position: relative;
    height: 0;
    padding-top: 70%;
    background-size: cover;
    .mask {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      background: $color-background;
      // background: transparent;
    }
    .filter {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(7, 17, 27, 0.4);
      // backdrop-filter: blur(20px);
    }
  }

  .song-list-wrap {
    position: fixed;
    width: 100%;
    bottom: 0;
    // overflow: hidden;
  }
}
</style>



