<template>
  <div class="list-view">
    <scroll ref="scroll" @scroll="scroll" :dataList="groupList">
      <div class="singer-group-list">
        <div
          v-for="group in groupList"
          :key="group.name"
          class="singer-group"
          ref="group"
        >
          <h2 class="group-title">{{ group.name }}</h2>
          <div class="singer-list">
            <div
              @click="onItemClick(singer)"
              v-for="singer in group.list"
              :key="singer.id"
              class="singer-item"
            >
              <img
                class="avatar"
                width="50"
                height="50"
                v-lazy="singer.avatar"
                alt=""
              />
              <span class="singer-name">{{ singer.name }}</span>
            </div>
          </div>
        </div>
      </div>
      <div
        class="anchor-list"
        @touchstart="onAnchorTouchStart"
        @touchmove.stop.prevent="onAnchorTouchMove"
      >
        <div
          v-for="(anchor, index) in anchorList"
          :key="index"
          class="anchor-item"
          :class="{ active: currentIndex === index }"
          :data-index="index"
        >
          {{ anchor }}
        </div>
      </div>
      <h2 ref="fixedTitleRef" class="fixed-title">{{ fixedTitle }}</h2>
    </scroll>
  </div>
</template>

<script>
import Scroll from '../../base/scroll/scroll'
import { getDataAttr } from '../../common/js/dom'
export default {
  name: '',
  data() {
    return {
      touch: {},
      currentIndex: 0
    }
  },
  props: {
    groupList: {
      type: Array,
      required: true,
      default() {
        return []
      }
    }
  },
  computed: {
    anchorList() {
      return this.groupList.map((e) => e.name.substring(0, 1))
    },
    fixedTitle() {
      return this.anchorList[this.currentIndex]
    }
  },
  created() {
    this.heightArray = []
  },
  mounted() {
    setTimeout(() => {
      let accHeight = 0
      this.$refs.group.forEach((ele, index, arr) => {
        if (index > 0) {
          accHeight += arr[index - 1].clientHeight
        }
        this.heightArray.push(accHeight)
      })
    }, 20)
  },
  watch: {
    // TODO: 监听index改变无效？？？？
    touch: {
      anchorIndex(newVal) {
        if (!newVal && newVal !== 0) {
          return
        }
        this._scrollToIndex(newVal)
      }
    }
  },
  methods: {
    onItemClick(item) {
      this.$emit('itemClick', item)
    },
    scroll(y) {
      if (y >= 0) {
        this.$refs.fixedTitleRef.style.display = 'none'
        this.currentIndex = 0
        return
      }
      y = Math.abs(y)
      if (y >= this.heightArray[this.heightArray.length - 1]) {
        this.$refs.fixedTitleRef.style.display = 'block'
        this.$refs.fixedTitleRef.style.transform = 'translate3d(0,0,0)'
        this.currentIndex = this.heightArray.length - 1
        return
      }
      this.$refs.fixedTitleRef.style.display = 'block'
      this.heightArray.some((e, i, arr) => {
        if (y >= e && y < arr[i + 1]) {
          if (arr[i + 1] - y <= 30) {
            this.$refs.fixedTitleRef.style.transform = `translate3d(0, -${
              y - (arr[i + 1] - 30)
            }px, 1px)`
          } else {
            this.$refs.fixedTitleRef.style.transform = 'translate3d(0,0,0)'
          }
          this.currentIndex = i
          return true
        }
      })
    },
    onAnchorTouchStart(event) {
      const target = event.target
      this.touch.startAnchorIndex = +getDataAttr(target, 'index')
      if (this.touch.startAnchorIndex || this.touch.startAnchorIndex === 0) {
        this.touch['pageY1'] = event.touches[0].pageY
        this.touch.anchorIndex = this.touch.startAnchorIndex
        this._scrollToIndex(this.touch.anchorIndex)
      } else {
        this.touch['pageY1'] = undefined
      }
    },
    onAnchorTouchMove(event) {
      // TODO: 处理点击两边padding的情况 歌手列表的滚动
      if (this.touch['pageY1']) {
        this.touch.pageY2 = event.touches[0].pageY
        this.touch.anchorIndex =
          this.touch.startAnchorIndex +
          Math.floor((this.touch.pageY2 - this.touch.pageY1) / 18)
        if (this.touch.anchorIndex < 0) {
          this.touch.anchorIndex = 0
        } else if (this.touch.anchorIndex > this.anchorList.length - 1) {
          this.touch.anchorIndex = this.anchorList.length - 1
        }
        this._scrollToIndex(this.touch.anchorIndex)
      }
    },
    _scrollToIndex(index) {
      this.$refs.scroll.scrollToElement(this.$refs.group[index])
    }
  },
  components: { Scroll }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable.scss';
.singer-group-list {
  .singer-group {
    .group-title {
      height: 30px;
      line-height: 30px;
      padding-left: 30px;
      background: $color-highlight-background;
      font-size: $font-size-small;
      color: $color-text-l;
    }
    .singer-list {
      .singer-item {
        display: flex;
        align-items: center;
        padding: 20px 0 0 30px;
        .avatar {
          margin-right: 20px;
          border-radius: 50%;
        }
        .singer-name {
          font-size: $font-size-medium;
          color: $color-text-l;
        }
      }
    }
  }
}
.anchor-list {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 10px; // TODO: 手指
  padding: 20px 0;
  width: 20px;
  background: $color-background-d;
  .anchor-item {
    padding: 3px;
    text-align: center;
    color: $color-text-l;
    font-size: $font-size-small;
    &.active {
      transform: scale(1.2);
      color: $color-theme;
      font-weight: bold;
    }
  }
}

.fixed-title {
  position: absolute;
  top: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  padding-left: 30px;
  background: $color-highlight-background;
  font-size: $font-size-small;
  color: $color-text-l;
}
.list-view {
  height: 100%;
  position: relative;
  overflow: hidden;
}
</style>



