<template>
  <ul class="music-list">
    <li v-for="song in songList" :key="song.id" class="music-item">
      <h2 class="name">{{ song.name }}</h2>
      <p class="desc">
        <span class="singer-name">{{ song.singer }}</span>
        <span class="album">{{ song.album }}</span>
      </p>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'MusicList',
  data() {
    return {}
  },
  props: {
    songList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  components: {}
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable.scss';
@import '~common/scss/mixin.scss';
.music-list {
  background: $color-background;
  .music-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px 0 0 20px;
    height: 64px;
    font-size: 14px;
    .name,
    .desc {
      @include no-wrap();
      line-height: 20px;
      color: $color-text;
    }
    .desc {
      margin-top: 4px;
      color: $color-text-d;
      .album {
        margin-left: 10px;
      }
    }
  }
}
</style>



