import axios from 'axios'
/**
 * get请求测试数据
 * @param {*} url 测试请求数据的url 目前统一放在/static/data下面
 */
export function getData(url) {
  return new Promise((resolve, reject) => {
    axios
      .get(url)
      .then((data) => {
        resolve(data.data)
      })
      .catch((err) => {
        reject(err)
      })
  })
}



