import _ from 'lodash'

export function getFullUrl(url, data) {
  url +=
    url.lastIndexOf('?') === url.length - 1
      ? convertParams(data)
      : `?${convertParams(data)}`
  return url
}

/**
 * 转换参数对象为url
 * @param {*} data 参数对象
 */
export function convertParams(data) {
  let url = ''
  for (let key in data) {
    if (!_.isNil(data[key])) {
      url += `&${key}=${encodeURIComponent(data[key])}`
    }
  }
  url = url.substring(1)
  return url
}



