import { jsonp } from '../common/js/jsonp'
import { commonParams, jsonpOpt } from './config'
export function getSingers() {
  const url = 'https://c.y.qq.com/v8/fcg-bin/v8.fcg'
  const param = Object.assign({}, commonParams, {
    channel: 'singer',
    page: 'list',
    key: 'all_all_all',
    pagesize: 100,
    pagenum: 1,
    hostUin: 0,
    needNewCode: 0,
    platform: 'yqq'
  })
  return jsonp(url, param, jsonpOpt)
}

export function getSingerDetail(singermid) {
  const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'
  const param = Object.assign({}, commonParams, {
    hostUin: 0,
    needNewCode: 0,
    platform: 'yqq',
    order: 'listen',
    begin: 0,
    num: 80,
    songstatus: 1,
    singermid
  })
  return jsonp(url, param, Object.assign({}, jsonpOpt, { name: 'jp2' }))
}



