export default class Song {
  constructor({ id, mid, name, singer, album, duration, image, url }) {
    this.id = id
    this.mid = mid
    this.name = name
    this.singer = singer
    this.album = album
    this.duration = duration
    this.image = image
    this.url = url
  }
}

/**
 * 格式化歌手数据创建自己的song对象
 * @param {Object} musicData 接口返回的歌曲数据
 */
export function createSong(musicData) {
  return {
    id: musicData.songid,
    mid: musicData.songmid,
    name: musicData.songname,
    singer: getSingerName(musicData.singer),
    album: musicData.albumname,
    duration: musicData.interval,
    image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
    url: `http://ws.stream.qqmusic.qq.com/${musicData.songid}.m4a?fromtag=46`
  }
}

/**
 * 格式化歌曲的歌手名字
 * @param {Array} singerList 歌手列表
 */
function getSingerName(singerList) {
  if (singerList && Array.isArray(singerList)) {
    return singerList.map((singer) => singer.name).join('/')
  } else {
    return ''
  }
}



