<template>
  <div class="recommend">
    <scroll ref="scroll" :dataList="dataList">
      <div class="recommend-container">
        <slider v-if="bannerList && bannerList.length">
          <div v-for="banner in bannerList" :key="banner.id">
            <a :href="banner.linkUrl">
              <img :src="banner.picUrl" @load="onBannerOnloaded" alt="" />
            </a>
          </div>
        </slider>
        <div class="recommend-disc">
          <h1>热门歌单推荐</h1>
          <div v-if="recommendList && recommendList.length" class="disc-list">
            <div
              v-for="item in recommendList"
              :key="item.dissid"
              class="disc-item"
            >
              <img class="disc-img" v-lazy="item.imgurl" alt="" />
              <div class="disc-info">
                <h4 class="disc-name">{{ item.dissname }}</h4>
                <p class="disc-desc">{{ item.creator.name }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </scroll>
  </div>
</template>

<script>
import Slider from '../../base/slider/slider'
import Scroll from '../../base/scroll/scroll'
import { getBanner, getRecommendList, NO_ERROR } from '../../api/recommend'
export default {
  name: 'Recommend',
  data() {
    return {
      bannerList: null,
      recommendList: null,
      title: 'Recommend',
      dataList: []
    }
  },
  components: {
    Slider,
    Scroll
  },
  created() {
    this._getBanner()
    this._getRecommendList()
  },
  mounted() {},
  methods: {
    onBannerOnloaded() {
      this.$refs.scroll.refresh()
    },
    _getBanner() {
      getBanner().then((data) => {
        if (data.code === NO_ERROR) {
          this.bannerList = data.data.slider
          this.dataList.push(this.bannerList)
        }
      })
    },
    _getRecommendList() {
      getRecommendList().then((data) => {
        if (data.code === NO_ERROR) {
          this.recommendList = data.data.list
          this.dataList.push(this.recommendList)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable.scss';
@import '~common/scss/mixin.scss';
.recommend {
  position: fixed;
  top: 88px;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.recommend-container {
  color: $color-theme;
  .recommend-disc {
    h1 {
      height: 65px;
      line-height: 65px;
      text-align: center;
      color: $color-theme;
    }
    .disc-list {
      .disc-item {
        display: flex;
        padding: 0 20px 20px;
        .disc-img {
          flex: 0 0 60px;
          margin-right: 20px;
          width: 0;
          height: 60px;
        }
        .disc-info {
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          font-size: $font-size-medium;
          .disc-name {
            color: $color-text;
            margin-bottom: 10px;
          }
          .disc-desc {
            color: $color-text-d;
            // @include exceed-hide(1.2, 2);
            @include exceed-line-hide(2);
          }
        }
      }
    }
  }
}
// }
</style>



