<template>
  <div class="slider-wrapper" ref="sliderWrapper">
    <div class="slider-group" ref="sliderGroup">
      <slot></slot>
    </div>
    <div v-if="slideItems && slideItems.length" class="dots">
      <span
        v-for="(item, index) in slideItems"
        @click="_goToPage(index)"
        :key="index"
        class="dot"
        :class="{ active: index === currentPageIndex }"
      ></span>
    </div>
  </div>
</template>

<script>
import BScroll from '@better-scroll/core'
import Slide from '@better-scroll/slide'
import { addClass } from '../../common/js/dom'

BScroll.use(Slide)
export default {
  name: 'Slider',
  data() {
    return {
      currentPageIndex: 0,
      slideItems: null,
      slide: null
    }
  },
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 3000
    },
    autoplay: {
      type: Boolean,
      default: true
    }
  },
  mounted() {
    this._initSliderWidth()
    this._initScroll()
  },
  methods: {
    _initSliderWidth() {
      const wraperWidth = this.$refs.sliderWrapper.clientWidth
      const sliderGroupEle = this.$refs.sliderGroup
      const children = Array.from(sliderGroupEle.children)
      sliderGroupEle.style.width = children.length * wraperWidth + 'px'
      children.forEach((child) => {
        addClass(child, 'slider-item')
        child.style.width = wraperWidth + 'px'
      })
      this.slideItems = children
    },
    _initScroll() {
      this.slide = new BScroll(this.$refs.sliderWrapper, {
        scrollX: true,
        scrollY: false,
        slide: {
          threshold: 100,
          loop: this.loop,
          interval: this.interval,
          autoplay: this.autoplay
        },
        click: true,
        momentum: false,
        bounce: false,
        stopPropagation: true
      })
      this.slide.on('slideWillChange', (page) => {
        this.currentPageIndex = page.pageX
      })
    },
    _goToPage(index) {
      this.slide.goToPage(index, 0)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable.scss';
.slider-wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  .slider-group {
    .slider-item {
      display: inline-block;
      img {
        width: 100%;
      }
    }
  }
  .dots {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
    .dot {
      display: inline-block;
      margin-right: 10px;
      width: 6px;
      height: 6px;
      background-color: rgba($color: $color-theme, $alpha: 0.6);
      border-radius: 3px;
      &.active {
        width: 12px;
      }
    }
  }
}
</style>



