<template>
  <div class="singers-container">
    <listview
      @itemClick="goSingerDetail"
      v-if="singerGroupList.length"
      :groupList="singerGroupList"
    ></listview>
    <transition name="slide">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import { getSingers } from '../../api/singer'
import { NO_ERROR } from '../../api/config'
import { mapMutations } from 'vuex'
import Scroll from '../../base/scroll/scroll'
import Singer from '../../common/js/model/singer'
import Listview from '../../base/listview/listview'

const HOT_KEY = 'hot'
const HOT_NAME = '热'
export default {
  name: 'Singers',
  components: {
    Scroll,
    Listview
  },
  data() {
    return {
      singerGroupList: []
    }
  },
  created() {
    this.touch = {}
    this._getSingers()
  },
  methods: {
    goSingerDetail(item) {
      this.setSinger(item)
      this.$router.push(`/singers/${item.id}`)
    },
    _getSingers() {
      getSingers().then((data) => {
        if (data.code === NO_ERROR) {
          this.singerGroupList = this._formatsingerGroupList(data.data.list)
        }
      })
    },
    _formatsingerGroupList(list) {
      const singerMap = {}
      singerMap[HOT_KEY] = {
        name: HOT_NAME,
        list: []
      }
      list.forEach((item, index) => {
        const singer = new Singer(item.Fsinger_mid, item.Fsinger_name)
        if (index < 10) {
          singerMap[HOT_KEY].list.push(singer)
        }
        const key = item.Findex
        if (!singerMap.hasOwnProperty(key)) {
          singerMap[key] = {
            name: key,
            list: []
          }
        }
        singerMap[key].list.push(singer)
      })
      const hot = []
      const rem = []
      for (let key in singerMap) {
        if (key === HOT_KEY) {
          hot.push(singerMap[key])
        } else if (/[a-zA-Z]/.test(key)) {
          rem.push(singerMap[key])
        }
      }
      const result = hot.concat(
        rem.sort((a, b) => {
          return a.name.charCodeAt(0) - b.name.charCodeAt(0)
        })
      )
      return result
    },
    ...mapMutations({
      setSinger: 'SET_SINGER'
    })
  }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable.scss';
.singers-container {
  position: fixed;
  top: 88px;
  bottom: 0;
  width: 100%;
  color: $color-theme;
}

.slide-enter-active,
.slide-leave-active {
  transition: transform 0.5s;
}
.slide-enter, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateX(100%);
}
</style>



