import OriginalJsonp from 'jsonp'
import { getFullUrl } from './convert-url'
export function jsonp(url, param, options) {
  return new Promise((resolve, reject) => {
    OriginalJsonp(getFullUrl(url, param), options, (error, data) => {
      if (error) {
        reject(error)
      } else {
        resolve(data)
      }
    })
  })
}



