<template>
  <div class="tab-container">
    <router-link tag="div" class="tab-item" to="/recommend"
      ><span class="name">推荐</span></router-link
    >
    <router-link tag="div" class="tab-item" to="/singers"
      ><span class="name">歌手</span></router-link
    >
    <router-link tag="div" class="tab-item" to="/rank"
      ><span class="name">排行</span></router-link
    >
    <router-link tag="div" class="tab-item" to="/search"
      ><span class="name">搜索</span></router-link
    >
  </div>
</template>
<script>
export default {
  name: 'Tab'
}
</script>

<style lang="scss" scoped>
@import '~common/scss/variable';
.tab-container {
  display: flex;
  align-items: center;
  height: 44px;
  .tab-item {
    flex: 1;
    text-align: center;
    font-size: $font-size-medium;
    &.active {
      color: $color-theme;
      .name {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          right: 0;
          bottom: -7px;
          height: 0;
          border-top: 2px solid $color-theme;
        }
      }
    }
  }
}
</style>


