/**
 * 往元素上添加类样式
 * @param {*} el dom元素
 * @param {*} className 样式类名
 */
export function addClass(el, className) {
  if (hasClass(el, className)) {
    return
  }
  let classArr = el.className.split(' ')
  if (classArr.length === 1 && classArr[0] === '') {
    classArr[0] = className
  } else {
    classArr.push(className)
  }
  el.className = classArr.join(' ')
}

/**
 * 检查元素上是否包含某个类样式
 * @param {*} el dom元素
 * @param {*} className 类样式名
 */
export function hasClass(el, className) {
  let reg = new RegExp('(^|\\s)' + className + '(\\s|$)')
  return reg.test(el.className)
}

/**
 * 获取元素data-attr的值
 * @param {*} ele htmlelement
 * @param {*} attr data-attr
 */
export function getDataAttr(ele, attr) {
  return ele.dataset[attr]
}

let eleStyle = document.createElement('div').style
let vendor = (() => {
  let transfromNames = {
    webkit: 'webkitTransform',
    Moz: 'MozTransform',
    O: 'OTransform',
    ms: 'msTransform',
    standard: 'transform'
  }
  for (let key in transfromNames) {
    if (eleStyle[transfromNames[key]] !== undefined) {
      return key
    }
  }
  return false
})()

export function prefixStyle(style) {
  if (vendor === false) {
    return false
  }
  if (vendor === 'standard') {
    return style
  }
  return vendor + style.charAt(0).toUpperCase() + style.substring(1)
}



